<template>
  <b-card :class="`award-card shadow ${marginClass}`" >

    <!--Company logo (must)-->
    <b-card-img :src="require(`../assets/${thumbnail}`)"  
      height="40" 
      class="mt-2 mb-3 p-0"
    />
    
    <!-- rating + star; OR-->
    <b-col v-if="rating && review" class="p-0">
      <b-row no-gutters class="justify-content-center mb-3">
        <b-icon-star-fill v-for="n in displayStarScale" :key="n"></b-icon-star-fill>
        <b-icon-star v-for="m in 5-displayStarScale" :key="m"></b-icon-star>
      </b-row>
      <p class="text-center mb-2 font-weight-bold h5"> {{ rating }} out of 5 stars</p>
      <p class="text-center mb-1">from {{ review }} reviews</p>
    </b-col>

    <!-- desc + X learn more-->
    <b-col v-else-if="testimony" class="p-0">
      <p class="award-testimony">{{testimony}}</p>
      <p class="text-center mb-0"><b-link href="#">Learn more</b-link></p>
    </b-col>

  </b-card>
</template>

<script>

export default {
  name: 'AwardCard',
  props: {
    thumbnail: String,
    testimony: String,
    rating: Number,
    review: Number,
    marginClass: String,
  },
  computed:{
    displayStarScale(){
      var solidStars = Math.round(this.rating)
      return solidStars
    },
  },
}
</script>

<style lang="scss">

  .award-card:hover{
    transform: scale(1.15);
    box-shadow: 0 0 2rem var(--midgray) !important;
    transition: all 1s ease;
    z-index: 4;
  }

</style>
